import { Pipe, PipeTransform } from '@angular/core';

export type FormFieldElement = HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement;

/**
 * Appends the `*` char on the field label, if necessary.
 *
 * #### Example:
 * ```html
 * <label for="myField"
 *   [innerHTML]="'My Field' | requiredFieldLabel:myField"
 * ></label>
 * <input #myField id="myField" ...
 * ```
 */
@Pipe({ name: 'requiredFieldLabel' })
export class RequiredFieldLabelPipe implements PipeTransform {

  readonly mark = "<span class='required'>*</span>";

  /**
   * @param value the span will be appended to this value
   * @param field appends the span whe matching field is not provided or when it's a required field.
   */
  transform(value: string, field?: FormFieldElement): string {
    const visible = typeof field === 'undefined' || field.required;
    return value && visible ? value + this.mark : value;
  }

}
