import {
  Directive, HostListener, ElementRef, Output, EventEmitter
} from '@angular/core';

/**
 * Trims the field value & the model value on change.
 *
 * Works with input & textarea.
 *
 * #### Example:
 * ```html
 * <input type="text" mcTrim [(ngModel)]=...
 * ```
 */
@Directive({
  selector: 'input[mcTrim],textarea[mcTrim]'
})
export class TrimDirective {

  @Output()
  ngModelChange = new EventEmitter();

  private element: HTMLInputElement | HTMLTextAreaElement;

  constructor(element: ElementRef) {
    this.element = element.nativeElement;
  }

  @HostListener('change', ['$event.target.value'])
  onChange(value: string): void {
    value = value.trim();
    this.element.value = value;
    this.ngModelChange.emit(value);
  }

}
