import { Injectable, Inject } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { filter } from 'rxjs/operators';
import { fromEvent } from 'rxjs/observable/fromEvent';

export function filterSameOrigin(window: Window) {
  return (msg: MessageEvent) => msg.origin === window.location.origin;
}

export function filterSameWindow(source: Window) {
  return (msg: MessageEvent) => msg.source === source;
}

/**
 * This service allows the client to send/receive message events
 * to/from other windows as long as both windows share the same origin.
 */
@Injectable()
export class MessageService {

  private window: Window;
  private message$: Observable<MessageEvent>;

  constructor(
    @Inject('Window') window: any,
  ) {
    this.window = window;
    this.message$ = fromEvent<MessageEvent>(this.window, 'message').pipe(
      filter(filterSameOrigin(window))
    );
  }

  /**
   * Provides a stream of messages coming from the given window.
   *
   * @param source filter messages that come from this window. Defaults to the parent window.
   */
  get(source = this.window.parent): Observable<MessageEvent> {
    const fn = filterSameWindow(source);
    return this.message$.pipe(filter(fn));
  }

  /**
   * Sends a message to the given target window.
   *
   * @param message a value that will be serialized and sent as a message.
   * @param target the message will be sent to this window. Defaults to the parent window.
   */
  post(message: any, target = this.window.parent): void {
    const sameOrigin = this.window.location.origin;
    target.postMessage(message, sameOrigin);
  }

}
