import { TranslateLoader, TranslateModuleConfig } from '@ngx-translate/core';
import { Observable } from 'rxjs/Observable';
import { Http, Response } from '@angular/http';
import { map } from 'rxjs/operators';
import { forkJoin } from 'rxjs/observable/forkJoin';

export function makeConfig(factoryFn: (...args: any[]) => TranslateLoader): TranslateModuleConfig {
  return {
    loader: {
      provide: TranslateLoader,
      useFactory: factoryFn,
      deps: [Http],
    },
  };
}

export class TranslateStaticLoader implements TranslateLoader {

  constructor(
    readonly http: Http,
    readonly prefix = 'i18n',
    readonly suffix = '.json',
    private apps: string[],
  ) {
    // empty
  }

  /**
   * Gets the translations from the server
   * @param lang
   */
  public getTranslation(lang: string): Observable<any> {
    const requests: any[] = [];
    const prefix = this.prefix;
    const suffix = this.suffix;

    // Load global
    requests.push(this.http.get(prefix + '/' + lang + '/' + lang + suffix));

    // Load app-specific i18n files
    this.apps.forEach((appName: string) => {
      const url: string = prefix + '/' + lang + '/' + appName + '_' + lang + suffix;
      requests.push(this.http.get(url));
    });

    return forkJoin<Response>(...requests).pipe(
      map(responses => {
        const first = responses.shift();
        const combined = first && first.json();

        let counter = 0;
        responses.forEach((res: Response) => {
          const translates = res.json();
          const appName: string = this.apps[counter];
          combined[appName.toUpperCase()] = translates[appName.toUpperCase()];
          counter++;
        });
        return combined;
      }),
    );
  }
}
