import { Injectable, Inject, Optional } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { map, mergeMap } from 'rxjs/operators';
import { of } from 'rxjs/observable/of';

import { LOCALE, LOCALE_STORAGE_KEY, LOCALE_QUERY_PARAM } from './constants';

/**
 * This service provides the current locale.
 * It looks for the locale on:
 * 1. a URL query param;
 * 1. a session storage key;
 * 1. Fallback to the default "en-US".
 */
@Injectable()
export class LocaleService {

  private window: Window;

  constructor(
    @Inject('Window') window: any,
    @Optional() private activated: ActivatedRoute,
  ) {
    this.window = window;
  }

  /**
   * Retrieves the current locale.
   * This stream re-emits the locale when the query param changes.
   */
  public getCurrent(): Observable<string> {
    return of(LOCALE.DEFAULT).pipe(
      // overrides with the locale on session storage
      map((defaultLocale: string) => {
        const sessionLocale = this.window.sessionStorage.getItem(LOCALE_STORAGE_KEY);
        return sessionLocale || defaultLocale;
      }),
      // overrides with the locale on query param
      mergeMap((locale: string) => {
        if (this.activated) {
          return this.activated.queryParams.pipe(
            map(params => {
              return params[LOCALE_QUERY_PARAM] || locale;
            }),
          );
        } else {
          return of(locale);
        }
      }),
    );
  }
}
