import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';

import { CustomValidatorDirective } from './directive/custom-validator.directive';
import { ShowErrorIconDirective } from './directive/show-error-icon.directive';
import { ProvideParentFormDirective } from './directive/provide-parent-form.directive';
import { StrictPatternDirective } from './directive/strict-pattern.directive';
import { TrimDirective } from './directive/trim.directive';

import { DateFormatPipe } from './pipe/date.format.pipe';
import { RequiredFieldLabelPipe } from './pipe/required-field-label.pipe';

import { Logger } from './service/logger/logger.service';
import { getDefaultLogLevel } from './service/logger/log-level';
import { MessageService } from './service/message/message.service';
import { LocaleService } from './service/locale/locale.service';

export function getWindow(): Window { return window; }

const declarations = [
  CustomValidatorDirective,
  TrimDirective,
  RequiredFieldLabelPipe,
  ProvideParentFormDirective,
  StrictPatternDirective,
  ShowErrorIconDirective,
  DateFormatPipe,
];

/** Mastercard's Utilities for Angular */
@NgModule({
  declarations,
  imports: [CommonModule],
  exports: [...declarations],
})
export class MastercardCommons {

  /**
   * Use this method in your root module to register our providers.
   */
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: MastercardCommons,
      providers: [
        Logger,
        MessageService,
        LocaleService,
        { provide: 'LogLevel', useFactory: getDefaultLogLevel },
        { provide: 'Window', useFactory: getWindow },
      ],
    };
  }

}
